<template>
  <div v-loading="loading" class="card h-100 flex-column relative">
    <div class="flex-row mt-1">
      <span @click="exit" class="ml-auto exit">
        <i class="fas fa-door-closed"></i>
        离开
      </span>
    </div>
    <div class="flex-row">
      <p>Round {{ round }}</p>
      <small class="ml-auto flex-item-center">
        progress {{ `${index + 1} / ${cards.length}` }}
      </small>
      <small class="ml-2 flex-item-center">
        pending {{ `${forgetCards.length}` }}
      </small>
    </div>
    <div class="card-body text-center">
      <p v-if="view === 'front'">
        {{ cards[index] ? cards[index].front : '' }}
      </p>
      <p v-else>
        {{ cards[index] ? cards[index].back : '' }}
      </p>
    </div>
    <div v-if="view === 'front'" class="ml-auto mt-2">
      <button class="pure-button" @click="toRemember">记得</button>
      <button class="pure-button ml-1" @click="toForget">不记得</button>
    </div>
    <button v-else class="ml-auto mt-2 pure-button" @click="toNext">下一张</button>
  </div>
</template>

<script>
import shuffle from '@/tools/shuffle';

export default {
  props: {
    title: {
      type: String,
      required: true,
    }
  },
  data() {
    return {
      view: 'front',
      loading: true,
      index: 0,
      originalCards: [],
      cards: [],

      round: 1,

      rememberCards: [],
      forgetCards: [],
    }
  },
  mounted() {
    this.getData();
  },
  methods: {
    toRemember() {
      this.rememberCards.push(this.cards[this.index]);
      document.getElementsByClassName('card-body')[0].classList.add('card-back');
      this.view = 'back';
    },
    toForget() {
      this.forgetCards.push(this.cards[this.index]);
      document.getElementsByClassName('card-body')[0].classList.add('card-back');
      this.view = 'back';
    },
    toNext() {
      if (this.index + 1 >= this.cards.length) {
        if (this.forgetCards.length > 0) {
          this.index = 0;
          this.cards = this.forgetCards;
          this.forgetCards = [];
        } else {
          this.round += 1;
          this.cards = this.originalCards;
          this.rememberCards = [];
          this.forgetCards = [];
          this.index = 0;
        }
      } else {
        this.index += 1;
      }

      document.getElementsByClassName('card-body')[0].classList.remove('card-back');
      this.view = 'front';
    },
    getData() {
      this.$axios.get(`${this.$url.cards}/${this.title}`)
      .then((resp) => {
        this.cards= resp.data.cards;
        console.log(this.cards);
        this.loading = false;
        this.originalCards = resp.data.cards;
      })
      .catch((err) => {
        console.log(err)
      })
    },
    confirm() {
      return window.confirm('You really want to quit?');
    },
    exit() {
      this.$router.replace('/');
    }
  },
  beforeRouteLeave(to, from, next) {
    if (this.confirm()) {
      this.$bus.$emit('changeMode', 'normal');
      window.$mode = 'normal';
      next();
    } else {
      next(false);
    }
  },
}
</script>

<style lang="scss" scoped>

@keyframes flipFront {
  from {
    transform: rotateY(180deg)
  }
  to {
    transform: rotateY(0deg)
  }
}
@keyframes slide {
  from {
    width: 0px;
    // transform: translateX(-100%)
    border: 1px solid goldenrod;
  }
  to {
    width: 600px;
    border: 1px solid goldenrod;
    // transform: translateX(0%);
  }
}

@keyframes flipBack {
  from {
    transform: rotateY(0)
  }
  to {
    transform: rotateY(180deg)
  }
}

.card {
  height: 400px;
  width: 600px;
  margin: auto;
  color: #777;
  .card-body {
    p {
      margin: 0;
    }
    padding: $pd-1;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    box-shadow: 0px 0px 10px 0 silver;
    height: 300px;
    color: black;
  }
  .card-back {
    animation-name: flipBack;
    transform: rotateY(180deg);
    p {
      transform: rotateY(180deg);
    }
    animation-duration: 1s;
  }
  .exit {
    color: #777;
    &:hover {
      color: black;
      cursor: pointer;
      i::before {
        content: "\f52b";
      }
    }
  }
}
</style>
